import { BASE_IMAGE_URL,getCart,saveCart,deleteCart } from '@/utils/api'

import uniNumberBox from '../../../components/uni-number-box.vue'
export default {
	components: {
		uniNumberBox
	},
	data() {
		return {
			loadingText: {
					contentdown: "上拉显示更多",
					contentrefresh: "正在加载...",
					contentnomore: "没有更多数据了"
				},
			list: [],
			loadingType: 0,
			page: 1,
			allPrice: 0,
			allNum: 0,
			indexList: [],
			allStatus: false,
			isEditStatus: false
		};
	},
	onShow(){
		let userInfo = uni.getStorageSync("userInfo");
		if(!userInfo){
			uni.navigateTo({
				url: '/pages/login/login'
			});
			return;
		}else{
			if(userInfo.m_role == 0){
				//游客
				uni.showModal({
					title: "提示",
					content: "无权浏览，如需使用请联系客服开通权限",
					showCancel: false,
					success(res) {
						if(res.confirm){
							uni.switchTab({
								url: '../home/home'
							})
						}
					}
				})
			}else if(userInfo.m_role == 1){
				//区域经销商
				this.getCart();
			}else if(userInfo.m_role == 2){
				//分销商
				this.getCart();
			}
			
		}
	},
	methods: {
		editAction(){
			if(this.isEditStatus){
				this.isEditStatus = !this.isEditStatus;
				this.allStatus = true;
				this.allStatusChange();
			}else{
				this.isEditStatus = !this.isEditStatus;
				this.allStatus = true;
				this.allStatusChange();
			}
		},
		async deleteCart(){
			if(!this.isEditStatus){
				let p = [];
				this.list.map(function(item, index){
					if(item.checked){
					  p.push(item);
					}
				 });
				 
				 if(p.length == 0){
					 uni.showToast({
						icon: 'none',
						duration: 1000,
						title: "请选择商品"
					});
					 return;
				 }
				
				uni.setStorageSync("itemList", p);
				uni.navigateTo({
					url: '/pages/post_order/post_order'
				});
					  
				return;
			}
			let ids = "";
			let that = this;
		  this.list.map(function(item, index){
			if(item.checked){
			  ids = that.list[index].ct_id+",";
			}
		  });
		  
		  let params = {
			ct_id: ids.substring(0, ids.length-1),
		  };
		  let result = await deleteCart(params);
		  if(result.code == 1000){
			wx.showToast({
				title: '删除成功',
				icon: 'none',
				duration: 1000
			});
			this.getCart();
		  }
		},
		inputChange(e){
			this.calcStatus();
		},
		changeChecked: function(item){
		  console.log(item);
		  item.checked = !item.checked;
		  this.calcStatus();
		},
		calcStatus: function(){
		  let that = this;
		  this.allNum = 0;
		  this.allPrice = 0;
		  this.indexList = [];
		  this.list.map(function(item, index){
			if(item.checked){
			  that.allNum++;
			  that.allPrice = parseFloat(parseFloat(that.allPrice) + parseFloat(item.g_price*item.ct_count)).toFixed(2);
			  that.indexList.push(index);
			}
			if(that.allNum == that.list.length){
			  that.allStatus = true;
			}else{
			  that.allStatus = false;
			}
		  });
		},
		
		allStatusChange: function(){
		  let that = this;
		  this.allStatus = !this.allStatus;
		  this.allPrice = 0;
		  this.allNum = this.list.length;
		  this.list.map(function(item, index){
			if(that.allStatus){
			  item.checked = true;
			  that.allPrice = parseFloat(parseFloat(that.allPrice) + parseFloat(item.g_price*item.ct_count)).toFixed(2) ;
			  that.indexList.push(index);
			}else{
			  item.checked = false;
			  that.indexList = [];
			  that.allPrice = 0;
			}
			
		  });

		},
		reduceAction: function(index){
		  this.addCart(index, 1);
		},
		addAction: function(index){
		  this.addCart(index, 0);
		},
		async addCart(index, type){
			if(type == 0){
				this.list[index].ct_count++;
			}else{
				if(this.list[index].ct_count != 1){
				  this.list[index].ct_count --;
				}
			};
			let params = {
				ct_openid: wx.getStorageSync("openid"),
				ct_g_id: this.list[index].ct_g_id,
				ct_count: this.list[index].ct_count,
				ct_id: this.list[index].ct_id
			};
			let result = await saveCart(params);
			if(result.code == 1000){
				this.calcStatus();
			}else{
				wx.showToast({
					title: '操作失败',
					icon: 'none',
					duration: 1000
				})
			}
		},
		
		async getCart(){
		  this.allPrice = 0;
		  this.allNum = 0;
		  this.allStatus = false;
		  let result = await getCart(uni.getStorageSync("openid"));
		  if(result.code == 1000){
			this.list = result.data;
			this.list.map(function(item){
			  item.checked = false;
			});
		  }else{
			uni.showToast({
				title: result.msg,
				icon: 'none',
				duration: 1000
			})
		  }
		},


	}
};

